import axios from 'axios';
import Vue from 'vue';
import router from './router.js'
import vm from '../main.js'

// ()
axios.interceptors.request.use(function(config) {
  //config   axios(config)
  
  // token  (axios,axios)
  let token = window.localStorage.getItem('user')
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : ''
  config.headers={
    token: token
  }
  
  //loading
  vm.bLoading = true;
  
  return config;// ,
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
  //response   axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//routerAPI,currentRoute :()string,fullPath
  if(response.data.err===2 && !currentRoute.includes('/login')){    //err2,'/login'
    
    router.replace({    //:,
      path:'/login',
      query:{p:currentRoute}    //,,, ()
    })  //
  }
  
  //loading
  vm.bLoading = false;
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
// window.axios=axios;  
export default axios;